document.getElementById('downloadButton').onclick = function () {
    var svgN = document.getElementById('svg');
    var svgData = new XMLSerializer().serializeToString(svgN);
    var svgBlob = new Blob([svgData], { type: 'image/svg+xml;charset=utf-8' });
    var svgUrl = URL.createObjectURL(svgBlob);
    var downloadLink = document.createElement('a');
    downloadLink.href = svgUrl;
    downloadLink.download = 'graph.svg';
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
};
function clearDropdownsWithFilenames() {
    var xgSelect = document.getElementById('xgFileSelect');
    while (xgSelect.hasChildNodes()) {
        xgSelect.removeChild(xgSelect.lastChild);
    }
    var opt1 = document.createElement('option');
    opt1.value = 'none';
    opt1.innerHTML = 'None';
    xgSelect.appendChild(opt1);
    var gbwtSelect = document.getElementById('gbwtFileSelect');
    while (gbwtSelect.hasChildNodes()) {
        gbwtSelect.removeChild(gbwtSelect.lastChild);
    }
    var opt2 = document.createElement('option');
    opt2.value = 'none';
    opt2.innerHTML = 'None';
    gbwtSelect.appendChild(opt2);
    var gamIndexSelect = document.getElementById('gamIndexSelect');
    while (gamIndexSelect.hasChildNodes()) {
        gamIndexSelect.removeChild(gamIndexSelect.lastChild);
    }
    var opt3 = document.createElement('option');
    opt3.value = 'none';
    opt3.innerHTML = 'None';
    gamIndexSelect.appendChild(opt3);
}
function populateDropdownsWithFilenames() {
    $.ajax({
        type: 'POST',
        url: BACKEND_URL + '/getFilenames',
        crossDomain: true,
        success: function success(response) {
            var xgSelect = document.getElementById('xgFileSelect');
            var xgSelectValue = xgSelect.options[xgSelect.selectedIndex].value;
            var gbwtSelect = document.getElementById('gbwtFileSelect');
            var gbwtSelectValue = gbwtSelect.options[gbwtSelect.selectedIndex].value;
            var gamIndexSelect = document.getElementById('gamIndexSelect');
            var gamSelectValue = gamIndexSelect.options[gamIndexSelect.selectedIndex].value;
            clearDropdownsWithFilenames();
            response.xgFiles.forEach(function (filename) {
                var opt = document.createElement('option');
                opt.value = filename;
                opt.innerHTML = filename;
                if (opt.value === xgSelectValue) {
                    opt.selected = 'true';
                }
                xgSelect.appendChild(opt);
            });
            response.gbwtFiles.forEach(function (filename) {
                var opt = document.createElement('option');
                opt.value = filename;
                opt.innerHTML = filename;
                if (opt.value === gbwtSelectValue) {
                    opt.selected = 'true';
                }
                gbwtSelect.appendChild(opt);
            });
            response.gamIndices.forEach(function (filename) {
                var opt = document.createElement('option');
                opt.value = filename;
                opt.innerHTML = filename;
                if (opt.value === gamSelectValue) {
                    opt.selected = 'true';
                }
                gamIndexSelect.appendChild(opt);
            });
        },
        error: function error(responseData, textStatus, errorThrown) {
            console.log('POST failed.');
        }
    });
}
function setUpWebsocket() {
    var ws = new WebSocket(BACKEND_URL.replace(/^http/, 'ws'));
    ws.onmessage = function (message) {
        console.log('Message received');
        populateDropdownsWithFilenames();
    };
    ws.onclose = function (event) {
        setTimeout(setUpWebsocket, 1000);
    };
    ws.onerror = function (event) {
        ws.close();
    };
}
window.onload = function () {
    var dsSelect = document.getElementById('dataSourceSelect');
    DATA_SOURCES.forEach(function (ds) {
        var opt = document.createElement('option');
        opt.value = ds.name;
        opt.innerHTML = ds.name;
        dsSelect.appendChild(opt);
    });
    var opt = document.createElement('option');
    opt.value = 'custom';
    opt.innerHTML = 'custom';
    dsSelect.appendChild(opt);
    document.getElementById('goButton').click();
    populateDropdownsWithFilenames();
    document.getElementById('redundantNodesCheckbox').checked = true;
    document.getElementById('compressedViewCheckbox').checked = false;
    document.getElementById('showReadsCheckbox').checked = true;
    document.getElementById('softClipsCheckbox').checked = true;
    document.getElementById('colorsHaplo2').checked = true;
    document.getElementById('colorsFwReads3').checked = true;
    document.getElementById('colorsRevReads4').checked = true;
    setUpWebsocket();
};